body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* App CSS */

.leaflet-container {
  width: 95%;
  height: 77.7vh;
  border-radius: 20px;
  border: thick double #32a1ce;
  position: relative !important;
  z-index: 0;
}

.marker-cluster {
  background-clip: padding-box;
  border-radius: 20px;
  background-color: rgba(104, 240, 180, 0.6);
  text-align: center;
}

.marker-cluster-small div {
  background-color: rgba(5, 201, 114, 0.6);
}

.marker-cluster div {
  width: 30px;
  height: 30px;
  margin-left: 5px;
  margin-top: 5px;
  text-align: center;
  border-radius: 15px;
}

.marker-cluster span {
  line-height: 30px;
}

.app {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  font-family: "Nunito", sans-serif;
  background-image: linear-gradient(33deg, rgb(4, 74, 92), rgb(151, 214, 211));
}

div.leaflet-control-zoom {
  visibility: hidden;
}

.radio-container {
  justify-content: space-around;
  align-items: center;
  margin-bottom: 20px;
  border-radius: 5px;
  padding: 10px;
  background-color: rgb(6, 141, 107);
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.452);
}

li {
  list-style-type: none;
}

sup.numberCategory {
  color: rgb(207, 207, 207);
}

button.button-category {
  font-size: 17px;
  font-weight: bold;
  color: white;
  margin-left: 10px;
  cursor: pointer;
  background: none;
  border: none;
}

button.button-category:hover {
  color: rgb(0, 51, 99);
}

label:hover {
  color: rgb(0, 51, 99);
}

ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

/* Search Bar CSS */

.searchInputs {
  margin-left: 0px;
  margin-top: 0px;
  display: flex;
}

.search input {
  background-color: white;
  border: 0;
  border-radius: 5px;
  border-top-right-radius: 0px;
  border-bottom-right-radius: 0px;
  font-size: 18px;
  padding: 15px;
  height: 30px;
  width: 300px;
}

.searchIcon {
  margin-left: -4px;
  height: 60px;
  width: 50px;
  border-radius: 5px;
  background-color: white;
  display: grid;
  place-items: center;
}

input:focus {
  outline: none;
}
.searchIcon svg {
  font-size: 35px;
}

p.info {
  text-align: center !important;
  margin: 0px;
  font-size: 15px;
}

div.dataResult {
  margin-top: 5px;
  width: 300px;
  height: 217px;
  background-color: white;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  overflow: hidden;
  overflow-y: auto;
  position: absolute;
  z-index: 99;
}

.dataResult::-webkit-scrollbar {
  display: none;
}

.dataResult .dataItem {
  width: 300px;
  height: 50px;
  display: flex;
  align-items: center;
  color: black;
}

.dataItem p {
  margin-left: 10px;
}
a {
  text-decoration: none;
}

a:hover {
  background-color: lightgrey;
}

button.searchBar {
  margin-left: -20px;
  margin-top: -3px;
  height: 65px;
  width: 65px;
  border: none;
  place-items: center;
  background-color: rgba(0, 0, 0, 0);
  /* background-image: url("searchIcon.svg"); */
}

#clearBtn {
  cursor: pointer;
  /* background-image: url("crossIcon.svg"); */
}
